import React from "react";

function Card(props) {
  return (
    <div className="container-fluid" style={{ padding: "0px" }}>
      <div
        class="adjust"
        style={{
          marginTop: " 9%",
          marginBottom: "7%"
        }}
      >
        <div
          className="wraps"
          style={{
            paddingLeft: "15px",
            paddingRight: "15px",
            borderRadius: "20px",
            borderWidth: " 2px",
            borderTopColor: "white",
            boxShadow: "0px 10px 10px #EEEEEE",
            transition: ".3s"
          }}
        >
          <div
            className="row"
            style={{
              borderWidth: "12px",
              borderColor: "black"
            }}
          >
            <div className="col-lg-2">
              <img
                src={props.img}
                className=".img-fluid"
                alt="img"
                height="140px"
                width="150px"
                style={{
                  borderRadius: "100px",
                  marginTop: "10px",
                  marginLeft: "20px",
                  marginBottom: "10px"
                }}
              />
            </div>
            <div
              className="col-lg"
              style={{ paddingLeft: "25px", paddingRight: "0px" }}
            >
              <div class="container-fluid" style={{ marginBottom: "15px" }}>
                <img
                  src="https://www.boloforms.com/_next/static/media/Stars.885edd0c.svg"
                  alt="img"
                  width="100px"
                  height="35px"
                  style={{ marginBottom: "7px" }}
                />
                <p style={{ marginBottom: "8px" }}>{props.content}</p>
                <h5>{props.name}</h5>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Card;
