import React from "react";

function Working() {
  return (
    <div className="container-fluid" style={{ padding: "0px" }}>
      <div
        className="work"
        style={{
          paddingTop: "80px",
          paddingBottom: "0px",
          margin: "0px"
        }}
      >
        <h2 className="use" style={{ textAlign: "center" }}>
          How does BoloForms work?
        </h2>
        <p style={{ textAlign: "center", marginRight: "0px" }}>
          Set up your first workflow in just 3 easy steps.
        </p>
        <div className="row" style={{ margin: "0px" }}>
          <div
            className="col-sm-4 card"
            style={{ width: "18rem", padding: "0px", borderColor: "white" }}
          >
            <img
              className="card-img-top"
              src="https://www.boloforms.com/_next/static/media/2.2709e204.svg"
              alt="Card image cap"
            />
            <div className="card-body" style={{ padding: "0px" }}>
              <h2 className="card-title">Step 1:</h2>
              <p className="card-text">
                Choose a template or add questions to create your Google Form.
              </p>
            </div>
          </div>

          <div
            className="col-sm-4 card"
            style={{ width: "18rem", padding: "0px", borderColor: "white" }}
          >
            <img
              className="card-img-top"
              src="https://www.boloforms.com/_next/static/media/1.b2cc23b0.svg"
              alt="Card image cap"
            />
            <div className="card-body" style={{ padding: "0px" }}>
              <h2 className="card-title">Step 2:</h2>
              <p className="card-text">
                Enable Single/Multi Step approval and add necessary approvers!
              </p>
            </div>
          </div>

          <div
            className="col-sm-4 card"
            style={{ width: "18rem", padding: "0px", borderColor: "white" }}
          >
            <img
              className="card-img-top"
              src="https://www.boloforms.com/_next/static/media/3.1a7ec00e.svg"
              alt="Card image cap"
            />
            <div className="card-body" style={{ padding: "0px" }}>
              <h2 className="card-title">Step 3:</h2>
              <p className="card-text">
                Now you can enable the workflow and share the Google Form.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
export default Working;
