import React from "react";

function BusinessNeeds() {
  return (
    <div className="row">
      <div className="col-md-6">
        <img src="imgae.jpg" alt="img" width="350px" height="425px" />
      </div>
      <div className="col-md-6">
        <h2
          style={{
            marginBottom: "16px",
            paddingRight: "30px",
            marginTop: "80px"
          }}
        >
          An End-To-End Workflow Platform For All Your Business Needs
        </h2>
        <p>
          1000+ companies-from Startups to Fortune 500s use BoloForms Approvals
          to build and manage business processes across departments.
        </p>

        <button
          type="button"
          class="btn btn-primary font-weight-bold btn-lg"
          style={{
            backgroundColor: "#9B71EF",
            borderRadius: "10px",
            borderWidth: "2px",
            borderColor: "#9B71EF",
            marginRight: "15px",
            marginBottom: "40px"
          }}
        >
          <div className="container-fluid">BoloForms Premium</div>
        </button>
      </div>
    </div>
  );
}

export default BusinessNeeds;
